import React, {useState} from "react"
import {Nav, Navbar, Button, Container, Modal, Form} from 'react-bootstrap'
import {Link} from "react-router-dom"
import styled from "styled-components"

const Styles = styled.div`
  a, .navbar-brand, .navbar-nav, nav-link {
    color: #adb1b8;

    &:hover {
      color: white;
    }
  }
`

export function NaviBar() {

    const [show, setShow] = useState(false)

    const handleClose = () => setShow(false)
    const handleShow = () => setShow(true)

    return <>
        <Styles>
            <Navbar collaseOnSelect extend="lg" bg="dark" variant="dark">
                <Container>
                    <Navbar.Brand>CS-GO без наёба</Navbar.Brand>
                    <Navbar.Toggle aria-controls="responsive-navbar-nav"/>
                    <Navbar.Collapse id="responsive-navbar-nav">
                        <Nav className="mr-auto">
                            <Nav.Link><Link to='/' className='' style={{textDecoration: 'none'}}>Home</Link></Nav.Link>
                            <Nav.Link><Link to='/profile' style={{textDecoration: 'none'}}>Profile</Link></Nav.Link>
                        </Nav>
                        <Nav>
                            <Button variant='primary' className='mr-2' onClick={handleShow}>Login</Button>
                            <Button variant='primary' className='mr-2' onClick={handleShow}>Logout</Button>
                        </Nav>
                    </Navbar.Collapse>
                </Container>
            </Navbar>
        </Styles>
        <Modal show={show} onHide={handleClose}>
            <Modal.Header closeButton>
                <Modal.Title>Log in</Modal.Title>
                <Modal.Body>
                    <Form>
                        <Form.Group controlId='formBasicEmail'>
                            <Form.Label>Email Adress</Form.Label>
                            <Form.Control type='email' placeholder='Enter email'/>
                            <Form.Text className='text-muted'>Well enter your email adress</Form.Text>
                        </Form.Group>
                        <Form.Group controlId='formBasicPassword'>
                            <Form.Label>Password</Form.Label>
                            <Form.Control type='password' placeholder='Enter Password'/>
                        </Form.Group>
                        <Form.Group controlId='formBasicCheckbox'>
                            <Form.Check type='checkbox' label='Remember me'/>
                        </Form.Group>
                    </Form>
                </Modal.Body>
            </Modal.Header>
        </Modal>
    </>
}