import React from "react"
import {Carousel} from "react-bootstrap"
import sl1 from '../../aceses/photos/slider_1.jpg'
import sl2 from '../../aceses/photos/slider_2.jpg'
import sl3 from '../../aceses/photos/slider_3.jpg'


export default function Slider (){
    return <Carousel>
        <Carousel.Item style={{'height': '500px'}}>
            <img className='d-block w-100' src={sl1} alt='First slide'/>
            <Carousel.Caption>
                <h3>New game</h3>
            </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item style={{'height': '500px'}}>
            <img className='d-block w-100' src={sl2} alt='First slide'/>
            <Carousel.Caption>
                <h3>CS-GO news</h3>
            </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item style={{'height': '500px'}}>
            <img className='d-block w-100' src={sl3} alt='First slide'/>
            <Carousel.Caption>
                <h3>Property tournament</h3>
            </Carousel.Caption>
        </Carousel.Item>
    </Carousel>
}
