import React, {Component, useEffect, useState} from 'react'
import {compose} from "redux";
import {Route, withRouter, HashRouter} from "react-router-dom";
import {Provider, connect} from "react-redux";
import store from "./redux/redux-store";
import Cases from "./componets/home/cases/Cases";
import ProfileContainer from "./componets/profile/ProfileContainer";
import CaseContainer from "./componets/case/CaseContainer";
import 'bootstrap/dist/css/bootstrap.min.css';
import Roulete from "./componets/case/roulet/Roulete";
import {getCase} from "./redux/case-reduser";
import Login from "./componets/Login/Login";
import {NaviBar} from "./componets/navibar/Navibar";
import {Footer} from "./componets/futer/Footer";
import {getProfile} from "./redux/profile-reduser";
import {Drop} from "./componets/header/drop/Drop";


function App(props) {

    const [cases, setCases] = useState([])

    useEffect(() => {
        setCases(props.cases)
        props.getCase()
    })
    debugger

    return <div style={{background: "lightgray"}}>
        <NaviBar/>
        <Drop guns={props.gun}/>
        <switch>
            <Route exact path={"/"} render={() => <Cases cases={cases}/>}/>
            <Route path={"/profile"} render={() => <ProfileContainer profile={props.profile} getProfile={props.getProfile}/>}/>
            <Route path={"/case/:id?"} render={() => <CaseContainer/>}/>
            <Route path={"/casego"} render={() => <Roulete/>}/>
            <Route path={"/login"} render={() => <Login/>}/>
        </switch>
        <Footer/>
    </div>
}

let mapStateToProps = (state) => {
    return {
        cases: state.cases.cases,
        profile: state.profile.profile,
        gun: state.app.gun
    }
}


let AppContainer = compose(withRouter, connect(mapStateToProps, {getCase, getProfile}))(App);

const MainApp = (props) => {
    return <HashRouter>
        <Provider store={store}>
            <AppContainer/>
        </Provider>
    </HashRouter>
}

export default MainApp