import React from 'react'
import Slider from "../../slider/Slider";
import {NavLink} from "react-router-dom";
import {grid, disc} from './Cases.module.css'

const Cases = (props) => {

    return (
        <div>
            <div>
                <Slider/>
            </div>
            <h1 style={{textAlign: "center", background: "lightgray"}}> Лучшие кейсы зимы!</h1>
            <div className={grid}>
                {
                    props.cases.map(c =>
                        <NavLink key={c.id} to={`/case/${c.id}`} style={{textDecoration: 'none'}}>
                            <img src={c.image}/>
                            <h3 className={disc}>{c.name}  {c.price}</h3>
                        </NavLink>
                    )}
            </div>
            <h1 style={{textAlign: "center", background: "lightgray"}}> Лучшие кейсы от шрека!</h1>
            <div className={grid}>
                {
                    props.cases.map(c =>
                        <NavLink key={c.id} to={`/case/${c.id}`} style={{textDecoration: 'none'}}>
                            <img src={c.image}/>
                            <h3 className={disc}>{c.name}  {c.price}</h3>
                        </NavLink>
                    )}
            </div>
            <h1 style={{textAlign: "center", background: "lightgray"}}> Лучшие кейсы на районе!</h1>
            <div className={grid}>
                {
                    props.cases.map(c =>
                        <NavLink key={c.id} to={`/case/${c.id}`} style={{textDecoration: 'none'}}>
                            <img src={c.image}/>
                            <h3 className={disc}>{c.name}  {c.price}</h3>
                        </NavLink>
                    )}
            </div>
            <h1 style={{textAlign: "center", background: "lightgray"}}> Лучшие кейсы осени!</h1>
            <div className={grid}>
                {
                    props.cases.map(c =>
                        <NavLink key={c.id} to={`/case/${c.id}`} style={{textDecoration: 'none'}}>
                            <img src={c.image}/>
                            <h3 className={disc}>{c.name}  {c.price}</h3>
                        </NavLink>
                    )}
            </div>
            <h1 style={{textAlign: "center", background: "lightgray"}}> Лучшие кейсы весны!</h1>
            <div className={grid}>
                {
                    props.cases.map(c =>
                        <NavLink key={c.id} to={`/case/${c.id}`} style={{textDecoration: 'none'}}>
                            <img src={c.image}/>
                            <h3 className={disc}>{c.name}  {c.price}</h3>
                        </NavLink>
                    )}
            </div>
        </div>
    )
}

export default Cases