import {caseAPI} from "../api/Api";
import s1 from "../aceses/photos/images.jpeg";

const SET_CASE = 'SET-CASE';

let initialState = {
    cases: [
        {id: 1, image: s1, name: 'Case 1', price: 100, rarity: 1, type_of_weapon: 1},
        {id: 2, image: s1, name: 'Case 2', price: 100, rarity: 1, type_of_weapon: 1},
        {id: 3, image: s1, name: 'Case 3', price: 100, rarity: 1, type_of_weapon: 1},
        {id: 4, image: s1, name: 'Case 4', price: 100, rarity: 1, type_of_weapon: 1},
        {id: 5, image: s1, name: 'Case 5', price: 100, rarity: 1, type_of_weapon: 1},
    ]
}



const caseReduser = (state = initialState, action) => {
    switch (action.type){
        case SET_CASE: {
            return {
                cases: action.cases,
            }

        }
        default:
            return state;
    }
}

export const setCase = (cases) => ({type: SET_CASE, cases})

export const getCase = () => async (dispatch) => {
    let response = await caseAPI.getCases();
    dispatch(setCase(response));
}

export default caseReduser;