import {appAPI} from "../api/Api";
import ph from "../aceses/photos/images.jpeg"

const SET_GUNS = 'SET-GUNS';

let initialState = {
    gun: [
        {image: ph, name: "пукалка", parents: "ANDREY"},
        {image: ph, name: "пистолет", parents: "ANDREY"},
        {image: ph, name: "пушка", parents: "ANDREY"},
        {image: ph, name: "глок", parents: "ANDREY"},
    ]


}


const appReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_GUNS: {

            return {
                ...state,
                gun: action.guns,
            }

        }
        default:
            return state;
    }
}

const setGuns = (guns) => ({type: SET_GUNS, guns})

const getGuns = () => async (dispatch) => {
    let response = await appAPI.getGuns();
    debugger
    dispatch(setGuns(response[0]));
}

export default appReducer;