import React from 'react'
import profile from './../../../aceses/photos/counter_strike.jpg'
import s from './ProfileInfo.module.css'

const ProfileInfo = (props) =>{
    return(
        <div className={s.grid}>
            <div className={s.photo}>
                <img src={profile}/>
            </div>
            <div className={s.disc}>
                <b>Full name: </b>{props.profile ? props.profile.user : "User"}<br/><br/>
                <b>Status: </b>{props.profile ? props.profile.status : " "}<br/><br/>
                <b>Money: </b>{props.profile ? props.profile.balance : "0"}<br/><br/>
            </div>
        </div>
    )
}

export default ProfileInfo