import React, {useState, useRef, useEffect} from 'react'
import './Roulete.css'
import gun1 from './../../../aceses/photos/Mag7.png'
import gun2 from './../../../aceses/photos/Ump45go.png'
import gun3 from './../../../aceses/photos/CSGO_base_weapon_p90.png'
import gun4 from './../../../aceses/photos/CSGO_base_weapon_p90.png'
import gun5 from './../../../aceses/photos/CSGO_base_weapon_p90.png'
import gun6 from './../../../aceses/photos/CSGO_base_weapon_p90.png'

const image = [
    <img key={gun1} src={gun1} alt="first"/>,
    <img key={gun2} src={gun2} alt="second"/>,
    <img key={gun3} src={gun3} alt="third"/>,
    <img key={gun4} src={gun4} alt="fourth"/>,
    <img key={gun5} src={gun5} alt="fifth"/>,
    <img key={gun6} src={gun6} alt="sixth"/>,
]

function Roulete () {
    const [activeIndex, setActiveIndex] = useState(0)

    useEffect(() => {
        const interval = setInterval(() => {
            setActiveIndex((current) =>
                current === image.length - 1 ? 0 : current + 1
            )
        }, 400)
        return () => clearInterval()
    }, []);

    const prevImageIndex = activeIndex ? activeIndex - 1 : image.length - 1

    const nextImageIndex = activeIndex === image.length - 1 ? 0 : activeIndex + 1

    return (
        <div className="slider">
            <div className="slider-image slider-image-prev"
                 key={prevImageIndex}>
                {image[prevImageIndex]}
            </div>
            <div className="slider-image"
                 key={activeIndex}>
                {image[activeIndex]}
            </div>
            <div className="slider-image slider-image-next"
                 key={nextImageIndex}>
                {image[nextImageIndex]}
            </div>
        </div>
    )
}

export default Roulete